import { ThemeProvider } from "@material-ui/core/styles";
import React, { useState } from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import theme from "./components/ui/Theme";
import Header from "./components/ui/Header";
import Footer from "./components/ui/Footer";

/*Pages*/
import Home from "./pages/Home";
import Projects from "./pages/Projects";
import Contact from "./pages/Contact";
import About from "./pages/About";

const routes = [
  { text: "Início", link: "/" },
  { text: "Projetos", link: "/projetos" },
  { text: "Sobre Mim", link: "/sobre" },
  { text: "Contato", link: "/contato" },
];

function App() {
  const [selectedLink, setSelectedLink] = useState(window.location.pathname);

  return (
    <ThemeProvider theme={theme}>
      <BrowserRouter>
        <Header
          selectedLink={selectedLink}
          setSelectedLink={setSelectedLink}
          routes={routes}
        />
        <Switch>
          <Route
            key={routes[0].link}
            exact
            path={routes[0].link}
            render={(props) => (
              <Home {...props} setSelectedLink={setSelectedLink} />
            )}
          />
          <Route
            key={routes[1].link}
            exact
            path={routes[1].link}
            render={() => <Projects />}
          />
          <Route
            key={routes[2].link}
            exact
            path={routes[2].link}
            render={() => <About />}
          />
          <Route
            key={routes[3].link}
            exact
            path={routes[3].link}
            render={() => <Contact />}
          />
        </Switch>
        {/* <Footer/> */}
        <Footer setSelectedLink={setSelectedLink} />
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;
