import React from "react";
import { Link } from "react-router-dom";
import { Grid, Typography, Button, useMediaQuery } from "@material-ui/core";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { ChevronRight as ChevronRightIcon } from "@material-ui/icons";

import sky from "../assets/sky.jpg";

const useStyles = makeStyles((theme) => ({
  heroContainer: {
    height: "90vh",
    padding: "3em",
    color: "white",
    [theme.breakpoints.down("sm")]: {
      padding: "2em",
    },
    transition: "background-color 1s",
    background: `linear-gradient(45deg, rgba(1,65,207, 0.5) 100%, rgba(255,87,51, 0.1) 0%), url(${sky}) center center no-repeat`,
    backgroundSize: "cover",
  },

  button: {
    marginTop: "1em",
    width: "18em",
    color: theme.palette.common.white,
    backgroundColor: theme.palette.common.razz,
    "&:hover": {
      backgroundColor: theme.palette.common.razz,
      opacity: "0.7",
    },
  },
}));

export default function Home(props) {
  const classes = useStyles();
  const theme = useTheme();
  const matchesXS = useMediaQuery(theme.breakpoints.down("xs"));

  return (
    <Grid container>
      <Grid
        item
        container
        direction="column"
        className={classes.heroContainer}
        justify="center"
      >
        <Typography variant="h3" align={matchesXS ? "center" : "left"}>
          Olá, sou Jorge Vinícius
        </Typography>
        <Typography variant="h4" align={matchesXS ? "center" : "left"}>
          Desenvolvedor Web em Aracaju, SE
        </Typography>

        <Button
          component={Link}
          to="/projetos"
          onClick={() => props.setSelectedLink("/projetos")}
          className={classes.button}
          style={{ alignSelf: matchesXS ? "center" : undefined }}
        >
          <Typography variant="body1">veja meus projetos</Typography>
          <ChevronRightIcon />
        </Button>
      </Grid>
    </Grid>
  );
}
